* {
    box-sizing: border-box;
    
    
}

.body {
    font-family: 'Montserrat', 'Verdana', sans-serif;
    font-weight: 400;
    font-style: normal;
    margin: 0;
}

.container {
    max-width: 1320px;
    min-width: 375px;
    margin: 0 auto;
    padding: 0 15px;
}

.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
  }

  .img-responsive {
      max-width: 100%;
      height: auto;
  }

  a {
      color: #ffff;
      text-decoration: none;
      &:hover {
        text-decoration: none;
       }
  }